* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

#error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}

nav {
  display: flex;
}

.home-container {
  display: flex;
  min-height: 100vh;
}

.home-left {
  width: 55vw;
}

.home-left header {
  display: flex;
  gap: 2%;
  margin-left: 10%;
  margin-top: 20%;
}

.home-logo {
  max-width: 100px;
}

.home-title {
  max-width: 400px;
}

.home-left div {
  margin: 2% 10% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-left div p {
  line-height: 2;
  font-size: 18px;
}

.home-left div a {
  background-color: #7ed957;
  padding: 20px;
  margin-top: 5%;
  width: 100px;
  text-align: center;
  text-decoration: none;
  border-radius: 50vh;
  color: #fff;
  font-size: 18px;
  border: #7ed957 solid 1px;
  transition: transform 0.2s ease;
}

.home-left div a:hover {
  transform: scale(1.1);
  border: #333 solid 1px;
}

.home-right {
  background-color: #7ed957;
  width: 45vw;
  padding-right: 50px;
  clip-path: polygon(20% 0, 100% 0, 100% 100%, 0% 100%);
}
